.slider3-container{
    width: 100vw;
    height: 100vh;

    position: relative;

    @media screen and (max-width:800px) {
        height: 100vh;
    }

    .slider2-bg{

        position: absolute;
       
        height: 100%;
        width: 100%;
        z-index: -10000;
        @media screen and (max-width:800px) {
            display: none;
        }

        img{
            height: 100%;
            width: 100%;
        }
    }

    .slider2-bg-tablet{

        position: absolute;
       
        height: 100%;
        width: 100%;
        z-index: -10000;
        display: none;
        @media screen and (max-width:800px) {
            display: block;

        }

        img{
            height: 100%;
            width: 100%;
        }
    }

    .slider2-bg-phone{

        position: absolute;
       
        height: 100%;
        width: 100%;
        z-index: -10000;
        display: none;
        @media screen and (max-width:480px) {
            display: block;
           height: 100%;
        }

        img{
            height: 100%;
            width: 100%;
        }
    }


    .items{
        z-index: 1000;
        display: flex;
        flex-direction: row;
        height: 100%;
        color: white;

        @media screen and (max-width:800px) {
            flex-direction: column;
            height: auto;
        }


        .text{
            width: 50%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-top: 90px;
            padding-right: 70px;
            padding-left: 70px;
            transition: 0.3s ease-in-out;
            // background-color: black;

            @media screen and (max-width:800px) {
                width: 100%;
                padding-right: 0;
                padding-left: 0;
                padding-top: 150px;
               

            }

            @media screen and (max-width:480px) {
                
                padding-left: 30px;
                padding-top: 0;

            }
            

            .con{
                height: 100%;
                width: 100%;

                display: flex;
                flex-direction: column;
                text-align: left;
                align-items: flex-start;
                justify-content: space-between;
                padding: 60px;
                padding-left: 120px;
                // background-color: bisque;
                
                @media screen and (max-width:800px) {
                    
                    // background-color: aquamarine;
                    padding: 0;
                    padding-left: 20px;
                    display: flex;
                    height: 500px;
                    margin-top: 0px;
                }

                @media screen and (max-width:480px) {
                    
                    // background-color: aquamarine;
                    padding: 0;
                    padding-left: 0px;
                    display: flex;
                    height: 600px;
                    margin-top: 90px;
                }

                

                .title{
                    display: flex;
                    flex-direction: row;
                    margin-top: 40px;
                    text-transform: uppercase;

                    @media screen and (max-width:800px) {
                        padding-left: 100px;
                        margin-top: 100px;
                        
                    }

                    @media screen and (max-width:480px) {
                    
                        // background-color: aquamarine;
                        padding-left: 0;

                       
                    }
                    span{
                        color: rgba($color: #fff, $alpha: .5);
                        font-size: 2rem;
                        margin-right: 10px;


                        
                    }
                }

                .info{
                    margin-bottom: 50px;

                    @media screen and (max-width:800px) {
                    
                        margin-left: 100px;
                        margin-top: 50px;
                    }

                    @media screen and (max-width:480px) {
                    
                        margin-left: 0;
                        padding: 10px;
                      
                    }
                    .job-title{
                        margin-bottom: 15px;
                        color: rgba($color: #fff, $alpha: .5);
                        text-transform: uppercase;
                        font-size: 1.7rem;
                    }

                    .name{
                        margin-bottom: 15px;
                        text-transform: uppercase;
                        font-size: 2.6rem;
                    }

                    p{
                        color: rgba($color: #fff, $alpha: .5);
                        margin-top: 25px;
                        max-width: 450px;
                        font-size: 1.1rem;
                        line-height: 25px;

                        @media screen and (max-width:480px) {
                    
                            max-width: 370px;
                        }
                    }
                }

                .dots{
                    display: flex;
                    flex-direction: row;
                    margin-top: -100px;
                    @media screen and (max-width:800px) {
                    
                        margin-left: 80px;
                        margin-top: 10px;
                    }
                    .dot{
                        height: 15px;
                        width: 15px;
                        background-color: rgba($color: #fff, $alpha: .5);
                        margin-left: 20px;
                        list-style: none;
                        border-radius: 50%;
                        cursor: pointer;

                        @media screen and (max-width:480px) {
                            height: 20px;
                            width: 20px;
                            margin-left: 30px;
                        }
        
                       

                    }

                    .dot.active{
                        background-color: white;
                    }
                }

            }

        }

        .photos{
            width: 50%;
            height: 100%;
            padding-top: 150px;
            padding-right: 150px;
            @media screen and (max-width:800px) {
                width: 100%;
                height: auto;
                padding: 0;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                margin-top: 0px;
                
            }

            
            .img-con{
                display: flex;
                justify-content: flex-start;
                align-items: flex-end;
                height: 100%;
                width: 100%;

                @media screen and (max-width:800px) {
                    justify-content: center;
                    align-items: center;
                    height: 60%;
                    width: 60%;
                margin-right: 20px;
                }

                @media screen and (max-width:480px) {
                    width: 100%;
                    background-color: aqua;
                    margin-top: 70px;
                    height: 100%;
                    display: none;
                }

                img{
                    height: 100%;
                    width: 100%;
                    object-fit: contain;
                }
            }

        }
    }

    
}