.header-container{

    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: white;
    position: fixed;

    top: 0;
    left: 0;
    z-index: 100 !important;

    
    

    .icon{
        padding: 40px;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            top: 45%;
            left: 140%;
            width: 500px;
            height: 1px;
            background-color: rgba($color: #fff, $alpha: .3);
            z-index: 11;

            @media screen and (max-width:800px) {
               display: none;
                
            }
        }
    }

    .links{
        margin-top: 20px;
        padding: 0px;
        width: 58%;

        @media screen and (max-width:800px) {
            display: none;
            
        }
       

        ul{
            display: flex;
            flex-direction: row;
            padding: 30px;
            backdrop-filter: blur(40px);
            background-color: rgba($color: #fff, $alpha: 0.05);
            
            
            li{
                color: white;
                list-style: none;
                 margin-left: 55px;   
                cursor: pointer;
                letter-spacing: 2px;
            }
        }
    }


    .link-tablet{
        display: none;
        position: fixed;
        top: 0;
        right: 0;

        ul{
            display: flex;
            flex-direction: row;
            padding: 40px;
            backdrop-filter: blur(40px);
            background-color: rgba($color: #fff, $alpha: 0.05);

            li{
                list-style: none;
                margin-right: 25px;
                font-size: .8rem;
                font-weight: 700;
                cursor: pointer;
            }
        }

        @media screen and (max-width:800px) {
            display: block;
             
         }


         @media screen and (max-width:480px) {
            display: none;
             
         }

    }




    
}