.slider2-container{
    width: 100vw;
    height: 100vh;

    position: relative;
    
    @media screen and (max-width:800px) {
        height: 100%; 
    }

    .slider2-bg{
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: -100000;

        @media screen and (max-width:800px) {
            display: none;
        }

        img{
            height: 100%;
            width: 100%;
        }
    }

    .slider2-bg-phone{
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: -100000;
        display: none;
        @media screen and (max-width:800px) {
            display: block;
        }

        img{
            height: 100%;
            width: 100%;
        }
    }

    .padding{
        padding-top: 20px;
        padding-bottom: 10px;

        @media screen and (max-width:800px) {
            padding-top: 0px;
        }
    }


    .items{
        z-index: 1000;

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        color: white;

        width: 100%;
        height: 100%;

        @media screen and (max-width:800px) {
            flex-direction: column; 
            height: 100vh;
        }

        
        .photos{
            width: 50%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center !important;

            .img-con{
                margin-top: 50px;
            }
            .title{
                font-size: 2rem;
            }

            @media screen and (max-width:800px) {
                width: 100%;
                margin-top: 120px;

                .title{
                    margin-bottom: 20px;
                    font-size: 2rem;
                }
                .img-con{
                    margin-top: 0px;
                }
            }

            @media screen and (max-width:480px) {
                margin-top: 400px;

                .img-con{
                    
                    img{
                        width: 50%;
                    }
                }
            }
            
        }

        .text{
            width: 50%;
            padding-left: 100px;
            padding-right: 100px;

            @media screen and (max-width:800px) {
                width: 100%;
            }

            .links{
                display: flex;
                align-items: flex-start;
                align-items: flex-start;
                margin-top: 100px;
                ul{
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    
                    li{
                        list-style: none;
                        margin-right: 40px;
                        cursor: pointer;

                        font-weight: 600;
                        text-transform: uppercase;

                    }

                    li.active{
                        position: relative;
                        &::after{
                            content: '';
                            position: absolute;
                            width: 100%;
                            height: 2px;
                            background: rgba($color: #fff, $alpha: 0.4);
                            bottom: 0;
                            top: 25px;
                            left: 0;
                            
                        }

                    }
                }
            }

            .title{
                text-align: left;
                font-size: 5rem;
                font-weight: 700;
                margin-left: 10px;
                letter-spacing: 10px;
            }

            p{
                max-width: 450px;
                text-align: left;
                line-height: 30px;

                @media screen and (max-width:800px) {
                    font-size: 1.3rem;
                    max-width: 570px;
                }

                @media screen and (max-width:480px) {
                
                }

                
            }

            .line{
                width: 100%;
                height: 1.5px;
                background: rgba($color: #fff, $alpha: .3);
                margin-bottom: 20px;
                margin-top: 30px;
            }

            .info{
                display: flex;
                flex-direction: row;

                p{
                    font-size: 0.7rem;
                    padding-bottom: 10px;
                    

                }
                .avg{

                }

                .est{
                    margin-left: 40px;
                }
            }
        }
    }
}

