.slider4-container{
    width: 100vw;
    height: 100vh;

    position: relative;
    // background-color: bisque;

    @media screen and (max-width:800px) {
        height: 100%;
    }

    @media screen and (max-width:480px) {
        padding: 20px;      
    }

    .bg-slider4{

        position: absolute;
       
        width: 100%;
        height: 100%;
        z-index: -10000;

        @media screen and (max-width:800px) {
            display: none;
        }

        img{
            height: 100%;
            width: 100%;
        }
    }

    .bg-slider4-tablet{

        position: absolute !important;
       
        width: 100%;
        height: 100%;
        bottom: 0;
        z-index: -10000;
        display: none;
        @media screen and (max-width:800px) {
            display: block;
            
        }

        img{
            height: 104%;
            width: 100%;
        }
    }

    .items{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        z-index: 1000;
        color: white;

        @media screen and (max-width:800px) {
            flex-direction: column;
        }

        .info{
            width: 60%;
            height: 100%;
            // background-color: aquamarine;
            padding-top: 120px;
            padding-bottom: 50px;
            padding-right: 100px;
            padding-left: 100px;

            @media screen and (max-width:800px) {
                width: 100%;
                padding-right: 0;
                padding-left: 0;
                margin-top: 50px;
            }

         
        

            .title{
                display: flex;
                flex-direction: row;
                margin-left: 60px;
                @media screen and (max-width:480px) {
                 margin-top: 0px;   
                }
                span{
                    font-size: 1.6rem;
                    color: rgba($color: #fff, $alpha: 0.5);
                }
                h1{
                    margin-left: 20px;
                }
            }

            .info-box{
                display: flex;
                flex-direction: row;
                align-items: flex-start;
                justify-content: center;
                margin-top: 100px;

                @media screen and (max-width:480px) {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                }

                .buttons{
                    @media screen and (max-width:480px) {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    }
                    
                    ul{
                        @media screen and (max-width:480px) {
                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            align-items: center;
                        }
                        li{
                            list-style: none;
                            background-color: rgba($color: #fff, $alpha: 0.5);
                            padding: 28px 35px;
                            margin: 25px;
                            border-radius: 50%;
                            font-size: 1.5rem;
                            font-weight: 700;
                            cursor: pointer;
                        }

                        li.active{
                            background-color: white;
                            color: black;
                        }
                    }
                }


                .text{
                    text-align: left;
                    padding-left: 55px;
                    margin-top: 23px;
                    
                    p{
                        color: rgba($color: #fff, $alpha: 0.5);
                        max-width: 500px;
                        font-size: 1.1rem;
                        line-height: 30px;
                    }
                    h1{
                        font-size: 3rem;
                        font-weight: 600;
                        width: 100%;
                        letter-spacing: 12px;
                        padding-top: 20px;
                        padding-bottom: 20px;
                    }
                }
            }
        }


        .photo{
            width: 40%;
            // background-color: bisque;
            height: 100%;

            @media screen and (max-width:800px) {
                width: 100%;
            }
            

            .photo-con{
                height: 100%;
                width: 100%;
                // background-color: black;
                display: flex;
                justify-content: flex-end;
                align-items: flex-end;

                @media screen and (max-width:800px) {
                    justify-content: center;
                }
                

                .img{
                    height: 100%;
                    width: 100%;

                    @media screen and (max-width:480px) {
                        object-fit: contain;
                    }
                    
                }

            }
        }
    }
}