.home-container{
    
    width: 100%;
    // height: 100vh;
    position: relative;
    display: flex;
    flex-direction: row;
    // overflow: hidden;
    
    @media screen and (max-width:800px) {
        height: 160vh;
    }

    .bg-image-1{
        height: 100Vh;
        width: 100%;
        position: absolute;
        z-index: -110;

        @media screen and (max-width:800px) {
            display: none;
        }
        img{
            
            z-index: -10;
            width: 100%;
            height: 100%;
        }
    }

    .bg-image-2{
        height: 103.9%;
        width: 100%;
        position: absolute;
        z-index: -110;
        display: none;

        @media screen and (max-width:800px) {
            display: block;
        }
        img{
            
            z-index: -10;
            width: 100%;
            height: 100%;
        }

    }

    .bg-image-3{
        height: 103.9%;
        width: 100%;
        position: absolute;
        z-index: -110;
        display: none;

        @media screen and (max-width:480px) {
            display: block;
        }
        img{
            
            z-index: -10;
            width: 100%;
            height: 100%;
            background-color: aquamarine;
        }

    }
}


.slider{
    height: 100%;
    width: 100%;
    // background-color: red;
    z-index: 20;
    // margin-top: 40px;
}

.menu-bg{
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 70%;
    background-color: white;
    z-index: 1000;
    transition: 0.5s ease-in-out;


    .button{
        width: 100%;
        display: flex;
        justify-content: flex-end;
        cursor: pointer;

        img{
            background-color: black;
            padding: 20px;

        }
    }
    ul{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        li{
            list-style: none;
            padding-top: 40px;
            padding-bottom: 40px;
            font-weight: 600;
            font-size: 1.2rem;
            cursor: pointer;
        }
    }
}

.menu-bg.active{
    width: 0;

    .button{
        display: none;
    }
    ul{
        display: none;
        opacity: 0;
    }
}

.link-phonee{
    position: fixed;
    top: 20px;
    right: 10px;
    // width: 100%;
    // height: 100vh;
    // padding: 10px;
    // background-color: black;
    z-index: 100;
    display: none;

    @media screen and (max-width:480px) {
        height: 100%;
        
        display: block;
    }


    img{

        background-color: white;
