.slider1-container{
    width: 100vw;
    height: 100%;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    color: white;

    @media screen and (max-width:800px) {
        display: flex;
        flex-direction: column;
        
    }

    .items{
        width: 50%;
        padding: 80px;

        @media screen and (max-width:800px) {
        width: 100%;
        padding: 0;
            
        }
        

        .text{
            // background-color: bisque;
            margin-top: 110px;
            display: flex;
            flex-direction: column;
            // justify-content: flex-start;
            align-items: flex-start;
            text-align: left;
            padding-left: 40px;

            @media screen and (max-width:800px) {
                justify-content: center;
                align-items: center;
                
            }

            @media screen and (max-width:480px) {
                  
                padding-left: 0;
                        
            }
            

            h2{
                font-size: 2rem;
                font-weight: 300;
                // padding: 40px 0;
                display: flex;
                text-align: left;
                padding-top: 40px;

                @media screen and (max-width:800px) {
                    
                    margin-top: 80px;
                    font-size: 1.5rem;
                }

                @media screen and (max-width:480px) {
                  
                    font-size: 1.1rem;
                }
            }

            h1{
                font-size: 7rem;
                font-weight: 900;
                // padding: 40px;
                display: flex;
                text-align: left;
                padding-top: 20px;
                letter-spacing: 10px;

                @media screen and (max-width:800px) {
                   font-weight: 700;
                   font-size: 8rem;
                   margin-top: 30px;
                    
                }

                @media screen and (max-width:480px) {
                    
                    font-size: 6rem;
                    text-align: center;
                        
                    }

            }

            p{
                padding-top: 40px;
                line-height: 30px;
                width: 77%;

                @media screen and (max-width:800px) {
                    margin-top: 40px;
                }

                @media screen and (max-width:480px) {
                  
                     font-size: 1rem;   
                }
            }
        }

        .explore{
            display: flex;
            align-items: center;
            justify-content: center;

            @media screen and (max-width:800px) {
                display: flex;
                flex-direction: column;
                margin-top: 100px;
            }
          .exp-con{
            background-color: white;
            border-radius: 50%;    
            width: 35%;
            margin-top: 80px;

            @media screen and (max-width:800px) {
                
                width: 25%;
                margin-bottom: 100px;
            }

            @media screen and (max-width:480px) {
                  
                width: 42%;
            }

            h1{
                padding: 80px 0px;
                color: black;
            }
          }
        }
    }
}